.calendarbtn {
    color: rgb(201, 83, 83);
    border: none;
    padding: 8px 16px;
    font-size: 1rem;
    border-radius: 4px;
    cursor: pointer;
}

.detailsbtn {
    /* background-color: #8fea95; */
    color: rgb(201, 83, 83);
    border: none;
    padding: 8px 16px;
    font-size: 1rem;
    border-radius: 4px;
    cursor: pointer;
}

.calendarbtn:hover, .detailsbtn:hover {
    opacity: 0.9;
}

