.calendarbtndetailsbtn {
    background-color: antiquewhite;
    width: 100%;
    height: 20px;
    /* box-sizing: border-box;  */
    /* display: block; */
}





@media (min-width: 374px) and (max-width: 1220px) {
    .calendarbtndetailsbtn {
        background-color:  antiquewhite;
        width: 110%;
        height: 20px;
    }
}



@media (min-width: 280px) and (max-width: 374px) {
    .calendarbtndetailsbtn {
        background-color:  antiquewhite;
        width: 155%;
        height: 20px;
    }
}



@media (min-width: 0px) and (max-width: 280px) {
    .calendarbtndetailsbtn {
        background-color:  antiquewhite;
        width: 170%;
        height: 20px;
    }
}


