.calendar-header {
    padding: 8px 16px; /* px-4 py-2 */
    display: flex;
    align-items: center;
  }
  
  .calendar-logo {
    margin-right: 8px; /* mr-2 */
    width: 48px; /* w-12 */
    height: 48px; /* h-12 */
  }
  
  .calendar-title {
    margin-right: 30px; /* mr-10 */
    font-size: 1.25rem; /* text-xl */
    color: #6b7280; /* text-gray-500 */
    font-weight: bold; /* font-bold */
  }
  
  .nav-button {
    background: none;
    border: none;
  }
  
  .nav-icon {
    cursor: pointer; /* cursor-pointer */
    color: #4b5563; /* text-gray-600 */
    margin: 0 8px; /* mx-2 */
  }
  
  .calendar-month {
    margin-left: 16px; /* ml-4 */
    font-size: 1.25rem; /* text-xl */
    color: #6b7280; /* text-gray-500 */
    font-weight: bold; /* font-bold */
  }
  













  
/* Responsive for 0px to 300px */
@media (min-width: 0px) and (max-width: 300px) {
    /* .calendar-header {
      flex-direction: column;
      align-items: flex-start;
    }
   */
    .calendar-title {
      font-size: 0.875rem; /* Smaller text */
      margin-right: 0;
    }
  
    .calendar-month {
      font-size: 1rem; /* Keep the text smaller */
      margin-left: 0;
      margin-top: 4px;
    }
  
    .nav-icon {
      margin: 0 2px; /* Reduce spacing between icons */
    }
  }
  
  /* Responsive for 300px to 370px */
  @media (min-width: 300px) and (max-width: 370px) {
    .calendar-header {
      padding: 4px 12px;
    }
  
    .calendar-logo {
      width: 40px; /* Slightly larger logo */
      height: 40px;
    }
  
    .calendar-title {
      font-size: 1rem;
      margin-right: 10px;
    }
  
    .calendar-month {
      font-size: 1rem;
      margin-left: 8px;
    }
  }
  
  