/* Container for the sidebar and other components */
.sidebar-container {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  border: 1px solid #e5e7eb;
  padding: 20px;
  background-color: #f9f9f9; /* Light background color */
  width: 256px;
}

/* Sidebar styling */
.sidebar {
  flex: 1; /* Take up available space */
  display: flex;
  flex-direction: column; /* Stack items vertically */
  gap: 10px; /* Space between items */
  background-color: #ffffff; /* White background for sidebar */
}








/* जब स्क्रीन की चौड़ाई 0px से 369px के बीच होगी (छोटे मोबाइल डिवाइस) */
@media (min-width: 301px) and (max-width: 369px) {
  .sidebar {
    width: 320px; /* पूरी चौड़ाई ले */
    padding: 10px; /* थोड़ी padding */
    border: none; /* कोई बॉर्डर नहीं */
    background-color: #f3f4f6; /* हल्का बैकग्राउंड */
    display: grid; /* ग्रिड लेआउट */
    grid-template-columns: repeat(2, 1fr); /* 3 रो, हर रो की ऊंचाई सामग्री के अनुसार होगी */
    /* gap: 10px;  */
  }


}









/* जब स्क्रीन की चौड़ाई 0px से 300px के बीच होगी */
@media (min-width: 0px) and (max-width: 300px) {
  .sidebar {
    width: 190%; /* पूरी चौड़ाई ले */
    /* padding: 5px;  */
    border: none; /* कोई बॉर्डर नहीं */
    background-color: #f3f4f6; /* हल्का बैकग्राउंड */
    display: grid; /* ग्रिड लेआउट */
    grid-template-columns: repeat(2, 1fr); /* 1 कॉलम, हर कॉलम की ऊंचाई सामग्री के अनुसार होगी */
    /* gap: 5px; */
  }
}
