.modal-overlay {
  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.event-modal {
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 500px; /* maximum width for larger screens */
  padding: 1.5rem;
}

.modal-header {
  background-color: #f3f4f6;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e5e7eb;
  border-radius: 0.5rem 0.5rem 0 0;
}

.header-icon {
  color: #9ca3af;
}

.close-button {
  background: none;
  border: none;
}

.delete-icon {
  cursor: pointer;
}

.modal-content {
  padding: 1.5rem;
}

.modal-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.input-field {
  padding-top: 0.75rem;
  border: none;
  border-bottom: 2px solid #e5e7eb;
  font-size: 1rem;
  color: #374151;
  outline: none;
  transition: border-color 0.2s ease-in-out;
}

.schedule-icon,
.bookmark-icon {
  color: #9ca3af;
}

.label-selector {
  display: flex;
  gap: 0.5rem;
}

.select-field {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-bottom: 2px solid #e5e7eb;
  font-size: 1rem;
  color: green;
  outline: none;
  transition: border-color 0.2s ease-in-out;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
  border-top: 1px solid #e5e7eb;
  margin-top: 1rem;
}

.save-button {
  background-color: #2563eb;
  color: red;
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .event-modal {
    width: 80%;
    padding: 1rem;
  }

  .modal-header {
    padding: 0.75rem;
  }

  .modal-content {
    padding: 1rem;
  }

  .save-button {
    padding: 0.5rem 1rem;
  }
}

@media (max-width: 480px) {
  .event-modal {
    width: 90%;
    padding: 0.75rem;
  }

  .modal-header {
    padding: 0.5rem;
  }

  .modal-content {
    padding: 0.75rem;
  }

  .save-button {
    padding: 0.5rem 1rem;
    font-size: 0.875rem;
  }
}









/* Media Queries for Smallest Screens (e.g., older smartphones) */
@media (max-width: 320px) {
  .event-modal {
    width: 95%;
    padding: 0.5rem;
  }

  .modal-header {
    padding: 0.5rem;
    font-size: 0.875rem; /* Make text smaller */
  }

  .modal-content {
    padding: 0.5rem;
  }

  .input-field {
    font-size: 0.875rem; /* Reduce input field font size */
  }

  .save-button {
    padding: 0.5rem;
    font-size: 0.75rem; /* Reduce button font size */
  }
}
