.out-calendar-view {
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .out-calendar-container {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .month-calendar-body {

    display: flex;
    flex: 1;
  }

  @media (min-width: 301px) and (max-width: 369px) {
    .onlyformonth {
      position: relative;
      top: 220px;
      margin-left: -340px;
      /* अन्य स्टाइल्स यहाँ जोड़ें */
    }
  }



  @media (min-width: 0px) and (max-width: 300px) {
    .onlyformonth {
      position: relative;
      top: 220px;
      margin-left: -150px;
      /* अन्य स्टाइल्स यहाँ जोड़ें */
    }
  }
  