.small-calendar-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px; /* Max width को कम किया */
}

.small-calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem; /* Padding को कम किया */
  background-color: #f3f4f6;
  border-bottom: 1px solid #e5e7eb;
}

.small-calendar-month {
  font-size: 1rem; /* Font size को छोटा किया */
  font-weight: bold;
}

.small-nav-button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.25rem; /* Font size को कम किया */
  color: #4b5563;
}

.small-nav-icon {
  font-size: 1.25rem; /* Icon size को छोटा किया */
}

.small-calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.25rem; /* Gap को कम किया */
  padding: 0.25rem; /* Padding को कम किया */
}

.small-calendar-day-label {
  text-align: center;
  font-weight: bold;
  font-size: 0.75rem; /* Font size को छोटा किया */
}

.small-calendar-day {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.25rem; /* Padding को कम किया */
  border: 1px solid #e5e7eb;
  cursor: pointer;
  border-radius: 0.25rem;
}

.small-current-day {
  background-color: #2563eb;
  color: white;
  border-radius: 50%;
  font-size: 0.75rem; /* Font size को छोटा किया */
}

.small-selected-day {
  background-color: #d1fae5;
  color: #4b5563;
}

.small-day-number {
  font-size: 0.75rem; /* Font size को छोटा किया */
}


@media (min-width: 0px) and (max-width: 370px) {
  .small-calendar-container{
    display: none;
    
  }
}